.sort {
  display: flex;
  align-items: center;
  position: relative;
  height: 48px;
  cursor: pointer;

  &__txt {
    font-weight: 700;
    font-size: 18px;
    line-height: 17px;
    margin: 0;
  }

  &__list {
    @include reset-list;
    padding-top: 13px;
    padding-bottom: 13px;
    border-radius: 10px;
    background-color: var(--secondary);
    width: 132px;
    position: absolute;
    bottom: -140px;
    right: 0;
    transition: all .4s ease;
    visibility: hidden;
    opacity: 0;

    &_opened {
      opacity: 1;
      visibility: visible;
    }
  }

  &__selected {
    display: inline-block;
    border-bottom: 1px dotted var(--accent);
    padding-bottom: 2px;
    color: var(--accent);
  }

  &__option {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 38px;
    font-weight: 400;
    font-size: 16px;
    line-height: 17px;
    font-weight: 700;
    cursor: pointer;
    transition: all .4s ease;

    &:hover {
      background-color: var(--secondary-light);
    }

    &_active {
      color: var(--accent);
      background-color: var(--secondary-light);
    }
  }
}